<template>
<a-col :span="16" :offset="4" class='col'>
    <a-form :form="form" @submit="handleSubmit" layout='horizontal'>
        <a-row type="flex" justify="center">
            <a-col :span="10">
                <a-form-item :wrapper-col="{ span: 20,offset: 2 }">
                    <a-input v-decorator="['user_name',{...validateRules('')}]" placeholder=""></a-input>
                </a-form-item>
            </a-col>
            <a-col :span="10">
                <a-form-item :wrapper-col="{ span: 20,offset: 2 }">
                    <a-radio-group v-decorator="['user_sex',{initialValue:'male'}]" buttonStyle="solid">
                        <a-radio-button value="male">
                            
                        </a-radio-button>
                        <a-radio-button value="female">
                            
                        </a-radio-button>
                    </a-radio-group>
                </a-form-item>
            </a-col>
        </a-row>
        <a-row type="flex" justify="center">
            <a-col :span="10">
                <a-form-item :wrapper-col="{ span: 20,offset: 2 }">
                    <a-input v-decorator="['user_wechatID',{...validateRules('ID')}]" placeholder="ID"></a-input>
                </a-form-item>
            </a-col>
            <a-col :span="10">
                <a-form-item :wrapper-col="{ span: 24 }">
                    <a-input v-decorator="['user_wechatNumber',{...validateRules('')}]" placeholder=""></a-input>
                </a-form-item>
            </a-col>
        </a-row>
        <a-row>
            <a-col :span="20" :offset="2">
                <a-form-item :wrapper-col="{ span: 22,offset: 1 }">
                    <a-input v-decorator="['user_phoneNumber',{...validateRules('',{validator:checkPhoneNumber})}]" placeholder="" @focus='handlePhoneConfirmFocus'></a-input>
                </a-form-item>
            </a-col>
        </a-row>
        <a-row type="flex" justify="center">
            <a-col :span="10">
                <a-form-item :wrapper-col="{ span: 20,offset: 2 }">
                    <a-input v-decorator="['user_password',{...validateRules('',{validator:checkPasswordToSecond})}]" type="password" placeholder=""></a-input>
                </a-form-item>
            </a-col>
            <a-col :span="10">
                <a-form-item :wrapper-col="{ span: 20,offset: 2 }">
                    <a-input v-decorator="['user_passValidator',{...validateRules('',{validator: checkPasswordToFirst})}]" type="password" placeholder="" @blur='handleConfirmBlur'></a-input>
                </a-form-item>
            </a-col>
        </a-row>
        <a-row type="flex" justify="center">
            <a-col :span="10">
                <a-form-item label="" :wrapper-col="{ span: 16, offset:2 }" :labelCol="{span: 4, offset:1}">
                    <a-select v-decorator="['user_depart',{initialValue:departLists[0]}]">
                        <a-select-option v-for='(item,index) in departLists' :value="item" :key="index">{{item}}</a-select-option>
                    </a-select>
                </a-form-item>
            </a-col>
            <a-col :span="10">
                <a-form-item label="" :wrapper-col="{ span: 16, offset:2}" :labelCol="{span: 4, offset:1}" class='form-item'>
                    <a-select v-decorator="['user_position',{initialValue:positionLists[0]}]">
                        <a-select-option v-for='(item,index) in positionLists' :value="item" :key="index">{{item}}</a-select-option>
                    </a-select>
                </a-form-item>
            </a-col>
        </a-row>
        <a-form-item :wrapper-col="{ span: 18,offset: 3 }">
            <a-button type="primary" htmlType="submit" size="large"></a-button>
        </a-form-item>
    </a-form>
</a-col>
</template>

<script>
import {
    validateRules
} from '../utils/validator'
export default {
    data() {
        return {
            form: this.$form.createForm(this),
            departLists: [], //
            positionLists: [], //
            confirmPassDirty: false, //
            confirmPhoneDirty: false,
            validateRules,
        }
    },
    methods: {
        handleSubmit(e) {
            e.preventDefault();
            this.form.validateFieldsAndScroll((err, values) => {
                if (!err) {
                    console.log(values)
                }
            });
        },
        checkPasswordToSecond(rule, value, callback) { //
            const form = this.form;
            if (value && this.confirmPassDirty) {
                form.validateFields(['user_passValidator'], {
                    force: true
                });
            }
            callback();
        },
        checkPasswordToFirst(rule, value, callback) { //
            const form = this.form;
            if (value && value !== form.getFieldValue('user_password')) {
                callback('');
            } else {
                callback();
            }
        },
        handleConfirmBlur(e) {
            const value = e.target.value;
            this.confirmPassDirty = this.confirmPassDirty || !!value;
        },
        handlePhoneConfirmFocus(){
            this.confirmPhoneDirty=true;
        },
        checkPhoneNumber(rule, value, callback) { //
            const form = this.form;
            let phoneRule = /^1[3|4|5|7|8][0-9]{9}$/;
            if(!value){
                this.confirmPhoneDirty=false;
                console.log()
            }
            if (this.confirmPhoneDirty && !phoneRule.test(value)) {
                callback('')
            }
            callback()
        },
        _initData() {
            this.departLists = ['', '', '', '', ''];
            this.positionLists = ['', '', '', ''];
        }
    },
    created() {
        this._initData();
    }
}
</script>

<style lang="scss" scoped>
.col {
    margin-top: 120px;
}
</style>
